'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import DesktopNavigation from './DesktopNavigation';
import MobileNavToggle from './MobileNavToggle';
import MobileSidebar from './MobileSidebar';

const Header = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  return (
    <>
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 50,
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        backdropFilter: 'blur(4px)',
        borderBottom: '1px solid rgba(74, 222, 128, 0.1)'
      }}>
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16 lg:h-20">
            
            {/* Mobile Menu Toggle - Left Side */}
            <div className="lg:hidden">
              <MobileNavToggle 
                isOpen={isMobileMenuOpen}
                onClick={toggleMobileMenu}
              />
            </div>

            {/* Logo */}
            <div className="flex-shrink-0">
              <Link href="/" className="flex items-center space-x-3">
                <div className="w-10 h-10 lg:w-12 lg:h-12 bg-green-500/10 rounded-lg flex items-center justify-center border border-green-500/20">
                  <span className="text-green-400 font-bold text-lg lg:text-xl">FG</span>
                </div>
                <div className="block">
                  <h1 className="font-bold text-green-400 text-sm sm:text-base md:text-lg lg:text-xl">
                    <span className="sm:hidden">FG</span>
                    <span className="hidden sm:inline md:hidden">FG Science</span>
                    <span className="hidden md:inline lg:hidden">FG Science Degree College</span>
                    <span className="hidden lg:inline">FG Science Degree College for Men</span>
                  </h1>
                </div>
              </Link>
            </div>

            {/* Desktop Navigation */}
            <div className="hidden lg:block">
              <DesktopNavigation isScrolled={isScrolled} />
            </div>


          </div>
        </div>
      </div>

      {/* Mobile Sidebar */}
      <MobileSidebar 
        isOpen={isMobileMenuOpen}
        onClose={closeMobileMenu}
      />
    </>
  );
};

export default Header;