'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';

const DesktopNavigation = ({ isScrolled }) => {
  const pathname = usePathname();

  const navItems = [
    { href: '/', label: 'Home' },
    { href: '/about', label: 'About' },
    { href: '/programs', label: 'Programs' },
    { href: '/facilities', label: 'Facilities' },
    { href: '/admissions', label: 'Admissions' },
    { href: '/contact', label: 'Contact' },
  ];

  return (
    <nav className="flex items-center space-x-8">
      {navItems.map((item) => {
        const isActive = pathname === item.href;
        
        return (
          <Link
            key={item.href}
            href={item.href}
            className={`relative px-3 py-2 text-sm font-medium transition-all duration-300 hover:scale-105 ${
              isActive ? 'text-green-400 font-semibold' : 'text-green-400/80 hover:text-green-400'
            }`}
          >
            {item.label}
            
            {/* Active indicator */}
            {isActive && (
              <span 
                className="absolute bottom-0 left-0 right-0 h-0.5 rounded-full bg-green-400/80 transition-colors"
              />
            )}
          </Link>
        );
      })}
    </nav>
  );
};

export default DesktopNavigation;